import React, { useState } from 'react'
import styled from 'styled-components'
import { Title, TabButton } from '../styled'
import Fade from 'react-reveal/Fade'

const Services = () => {
  const [selectedTab, setSelectedTab] = useState('web')

  return (
    <Section>
      <Wrapper>
        <Fade>
          <Container>
            <Title>Services</Title>
          </Container>
        </Fade>

        <Content>
          {/* {selectedTab === 'web' && <div>WEB</div>}

            {selectedTab === 'mobile' && <div>MOBILE</div>} */}
          <Service />
          <Service />
          <Service />
          <Service />
          <div
            style={{
              height: '100%',
              flex: '0 0 20px',
              backgroundColor: 'black'
            }}
          />
        </Content>

        <Container>
          <Footer>
            <TabButton
              active={selectedTab === 'web'}
              onClick={() => setSelectedTab('web')}
            >
              Web
            </TabButton>

            <TabButton
              active={selectedTab === 'mobile'}
              onClick={() => setSelectedTab('mobile')}
            >
              Mobile
            </TabButton>
          </Footer>
        </Container>
      </Wrapper>
    </Section>
  )
}

export default Services

const Service = styled.div`
  flex: 0 0 100%;
  max-width: 300px;
  height: 100%;
  margin: 0 20px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 10px;

  @media screen and (max-width: 768px) {
    flex: 0 0 80vw;
    margin: 0 10px;
  }
`

const Container = styled.div`
  width: 90%;
  max-width: 1400px;
  display: flex;
  margin: auto;
`

const Section = styled.section`
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  width: 100%;
  max-width: 1400px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  margin-bottom: 20vh;
  margin-bottom: calc(var(--vh, 1vh) * 20);

  @media screen and (max-width: 768px) {
    height: 110vh;
    height: calc(var(--vh, 1vh) * 110);

    margin-bottom: 10vh;
    margin-bottom: calc(var(--vh, 1vh) * 10);
  }
`

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  padding-top: 6vh;
  padding-top: calc(var(--vh, 1vh) * 6);
  padding-bottom: 6vh;
  padding-bottom: calc(var(--vh, 1vh) * 6);
  flex: 1;
`

const Content = styled.div`
  flex: 1;
  -webkit-overflow-scrolling: touch;
  position: relative;
  margin-bottom: 40px;
  display: flex;
  overflow-x: auto;
  padding: 0 20px;
  scrollbar-width: none; /* Firefox 64 */
  -ms-overflow-style: none; /* IE 11 */
  &::-webkit-scrollbar {
    /** Webkit */
    display: none;
  }
`

